.top {
    position: relative;
    min-height: 100vh;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    &__inner {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 100vh;
    }

    h1 {
        max-width: 800px;
        font-weight: 600;
        font-size: 64px;
        line-height: 72px;
        letter-spacing: -0.04em;
        text-align: center;
        text-transform: uppercase;
    }

    &__scroll {
        position: absolute;
        left: 50%;
        bottom: 88px;
        display: flex;
        align-items: center;
        margin: 0;
        padding: 0 0 0 60px;
        height: 48px;
        font-size: 12px;
        line-height: 20px;
        transform: translateX(-50%);

        &::after {
            content: url("../../images/top-scroll.svg");
            position: absolute;
            left: 0;
            top: 0;
            width: 48px;
            height: 48px;
        }
    }
}

.do {
    padding: 200px 0;
    background-color: #010c21;
}